export default{
	data(){
		return{
			height:'',
			userid:'',
		}
	},
	onLoad(e){
	this.userid=e.id;
	},
	onReady(){
			this.getTopheight();
	},
	methods:{
			getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
				}
			})
		},
		apply:function(){
			uni.navigateTo({
				url:"/pages/Merchan_registration/Merchan_registration?id="+this.userid,
			})
		}
	}
}
